.addPopup {
	position: fixed;
	bottom: 32px;
	right: 32px;
	display: flex;
	align-items: center;
	justify-content: center;

	width: 64px;
	height: 64px;
	border-radius: 999px;
	font-size: 28px;
	font-weight: 900;
	color: var(--light);
	background-color: var(--primary);
	background-image: linear-gradient(to bottom right, var(--primary), var(--secondary));
	cursor: pointer;
}

.popup {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	width: 100%;
	max-width: 400px;
	background-color: var(--light);
	padding: 32px;
	border-radius: 16px;
	box-shadow: 0px 4px 32px var(--dark);
  
}

.closePopup {
	position: absolute;
	top: 16px;
	right: 16px;
	width: 20px;
	height: 20px;
	font-size: 25px;
	color: var(--dark);
	cursor: pointer;
}

.popup h3 {
	color: var(--dark);
	margin-bottom: 36px;
	font-weight: 500;
	text-transform: uppercase;
}

.popup input{
  font-size: 2rem;
  border: 2px groove var(--secondary);
  border-radius: 4px;
}
.popup button{
  background-color: var(--dark-alt);
  color:white;
  font-size:20px;
  padding: 5px 40px;
  border-radius: 5px;
  margin-top: 1rem;
  cursor: pointer;
}

@media only screen and (max-width: 500px){
  .addPopup {
    position: fixed;
    bottom: 32px;
    right: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  
    width: 74px;
    height: 74px;
    border-radius: 50%;
    font-size: 38px;
    font-weight: 900;
    color: var(--light);
    background-color: var(--primary);
    background-image: linear-gradient(to bottom right, var(--primary), var(--secondary));
    cursor: pointer;
  }
  
  .popup {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
  
    width: 80%;
    max-width: 400px;
    background-color: var(--light);
    padding: 32px;
    border-radius: 16px;
    box-shadow: 0px 4px 32px var(--dark);
  }
  
  .closePopup {
    position: absolute;
    top: 16px;
    right: 16px;
    width: 20px;
    height: 20px;
    font-size: 30px;
    color: var(--dark);
    cursor: pointer;
  }
  
  .popup h3 {
    color: var(--dark);
    margin-bottom: 36px;
    font-weight: 500;
    text-transform: uppercase;
  }

  .popup input{
    font-size: 1.5rem;
  }
  .popup button{
    background-color: var(--dark-alt);
    color:white;
    font-size:20px;
    padding: 5px 40px;
    border-radius: 5px;
    margin-top: 1rem;
    cursor: pointer;
  }
}